/* eslint-disable react/destructuring-assignment */
/* eslint react/prop-types: 0 */

// Components
import React, { Component } from 'react';
import { graphql } from 'gatsby';

import 'gitalk/dist/gitalk.css';

import { parseChineseDate } from '../api';

import ExternalLink from '../components/ExternalLink';
import Sidebar from '../components/Sidebar';
import Content from '../components/Content';
import SEO from '../components/SEO';

import Header from '../components/Header';
// import TableOfContent from '../components/TableOfContent';

import { config } from '../../data';

// Styles
import './blog-post.scss';

const { name, iconUrl, gitalk } = config;

const bgWhite = { padding: '10px 30px', background: 'white' };

// Prevent webpack window problem
const isBrowser = typeof window !== 'undefined';
const Gitalk = isBrowser ? require('gitalk') : undefined;

class BlogPost extends Component {
  constructor(props) {
    super(props);
    this.data = this.props.data;
  }

  componentDidMount() {
    const { title, id, id: graphqlId } = this.data.content.edges[0].node;

    const GitTalkInstance = new Gitalk({
      ...gitalk,
      title,
      id: id || graphqlId
    });
    GitTalkInstance.render('gitalk-container');
  }

  render() {
    const { node } = this.data.content.edges[0];

    const { content, date, headerImage, title, slug, description } = node;

    return (
      <div className="row post order-2">
        <Header
          img={headerImage.publicURL || 'https://i.imgur.com/M795H8A.jpg'}
          title={title}
          authorName={name}
          authorImage={iconUrl}
          subTitle={parseChineseDate(date)}
        />
        <Sidebar />
        <div className="col-xl-7 col-lg-6 col-md-12 col-sm-12 order-10 content">
          <Content post={content} />
          <div className="m-message" style={bgWhite}>
            <ExternalLink href="https://github.com/HellCatVN/" title="Github" />
          </div>

          <div id="gitalk-container" />
        </div>

        <SEO
          title={title}
          url={slug}
          siteTitleAlt="HellCatVN's Blog"
          isPost={false}
          description={description}
          image={headerImage.publicURL || 'https://i.imgur.com/M795H8A.jpg'}
        />
      </div>
    );
  }
}

export const pageQuery = graphql`
  fragment post on StrapiArticle {
    id
    title
    description
    slug
    content
    date
    headerImage {
      publicURL
    }
  }
  query BlogPostQuery($index: Int) {
    content: allStrapiArticle(
      sort: { order: DESC, fields: date }
      skip: $index
      limit: 1
    ) {
      edges {
        node {
          id
          ...post
        }
        previous {
          ...post
        }
        next {
          ...post
        }
      }
    }
  }
`;

export default BlogPost;
